.overview-stats {
  width: 100%;
  height: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
 
  .inner-overview-stats {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    gap: 30px;
    .list-and-chart {
      width: 100%;
      height: 350px;
      border-radius: 3px;
      box-shadow: #dfe5f9 0px 1px 3px 0px, #dfe5f9 0px 0px 0px 1px;
      display: flex;
      justify-content: center;
      align-items: flex-start;

      .test-list {
        width: 20%;
        height: 100%;
        border-right: 1.5px solid #dfe5f9;
      }

      .linechart-section {
        width: 80%;
        height: 100%;

        .heading-dropdown {
          width: 100%;
          display: flex;
          justify-content: space-between;
          height: 15%;

          padding: 14px;
          border-bottom: 1.5px solid #dfe5f9;
          h4 {
            color: #525b6c;
            font-size: 0.8rem;
            font-weight: 600;
          }
          .drop-downs {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 10px;
          }
        }

        .charts-section-stats {
          width: 100%;
          height: 85%;
          overflow-x: scroll;
        }
        ::-webkit-scrollbar {
            height: 4px;
          }
          
          /* Track */
          ::-webkit-scrollbar-track {
            background: #FAFBFF; 
          }
           
          /* Handle */
          ::-webkit-scrollbar-thumb {
            background: #E7ECFB; 
          }
      }
    }

    .table-info{
      width: 70%;
      height: 500px;
      box-shadow: #dfe5f9 0px 1px 3px 0px, #dfe5f9 0px 0px 0px 1px;
    }
  }
}
